<template>
  <div>
    <h3> 管理员列表</h3>
    <Divider orientation="left">列表筛选</Divider>
    <Row class="search">
      <Form inline :model="formData" :label-width="80">
        <FormItem label="编号">
          <Input v-model="formData.id" placeholder="请输入编号" class="filter-input"></Input>
        </FormItem>
        <FormItem label="真实姓名">
          <Input v-model="formData.name" placeholder="请输入名称" class="filter-input"></Input>
        </FormItem>
      </Form>
      <div class="filter-btn">
        <span class="span">操作 </span>
        <Button style="font-weight: bold" type="info" icon="ios-search" @click="loadData(1)"> 搜索</Button>
        <Button style="font-weight: bold" type="warning" icon="md-refresh" @click="ref()">重置</Button>
        <Button style="font-weight: bold" type="success" icon="md-add" @click="add()">新建</Button>
      </div>
    </Row>
    <Divider orientation="left">列表数据</Divider>
    <Table stripe border :columns="columns" :data="listData.data"></Table>
    <Spin size="large" fix v-if="load"></Spin>
    <Row v-if="listData.last_page > 1" style="text-align: center;margin-top: 20px">
      <Page @on-change="loadData" :total="listData.total" :page-size="listData.per_page"
            :current="listData.current_page" size="small"/>
    </Row>
  </div>
</template>

<script>
import api from './api'

export default {
  name: 'index',
  data () {
    return {
      formData: {},
      listData: {},
      teams: {},
      load: false,
      columns: [
        {
          title: 'id',
          align: 'center',
          key: 'id'
        },
        {
          title: '名称',
          align: 'center',
          key: 'name'
        },
        {
          title: '创建日期',
          align: 'center',
          key: 'create_time'
        },

        {
          title: '地址',
          align: 'center',
          key: 'address'
        },
        {
          title: '运营团队',
          key: 'operation_team',
          align: 'center',
          render: (h, params) => {
            return h('span', params.row.operation_team ? params.row.operation_team.name : '')
          }
        },
        {
          title: 'Action',
          width: 200,
          align: 'center',
          render: (h, params) => {
            return h('div', [
              h('Button', {
                props: {
                  type: 'success',
                  size: 'small'
                },
                style: {
                  marginRight: '5px'
                },
                on: {
                  click: () => {
                    this.add(params.row.id)
                  }
                }
              }, '编辑'),
              h('Button', {
                props: {
                  type: 'error',
                  size: 'small'
                },
                style: {
                  marginRight: '5px'
                },
                on: {
                  click: () => {
                    this.del(params.row)
                  }
                }
              }, '删除')
            ])
          }
        }

      ]
    }
  },
  activated () {
    this.loadData()
  },
  methods: {
    loadData (page) {
      if (page) {
        this.formData.jump_page = page
      }

      this.load = true
      api.index(this.formData, res => {
        this.listData = res.data.data
        this.load = false
      },
      () => {
        this.load = false
      })
    },
    add (id) {
      this.$router.push({
        name: 'spaceEdit',
        query: {
          id: id
        }
      })
    },
    ref () {
      this.formData = {}
      this.loadData(1)
    },
    del (item) {
      this.$Modal.confirm({
        title: '确定删除' + item.name + '吗?',
        okText: '删除',
        cancelText: '取消',
        loading: true,
        onOk: () => {
          api.delete({ id: item.id }, () => {
            this.$Modal.remove()
            this.$Message.success('删除成功')
            this.loadData(1)
          },
          () => {
            this.$Modal.remove()
          })
        }
      })
    }
  }
}
</script>
